
.header_container {

    background-image: url(../img/unnamed.jpg) ;
    height: 250px;
    box-sizing:content-box;
    margin:0;
    padding:0;
    display: grid;
    grid-template-columns: repeat(8,12.5%);
    grid-template-rows: 50% 50%;
    
}

.header_container>div {
    font-size: 40px;
    font-weight: bold;
    color: white;
    
    grid-row: 2/3;
    text-align: match-parent;
   
   
}
.header_container>div:nth-child(2) {
    grid-column: 6 / 7;
    line-height: 300%;
}
.header_container>div:nth-child(3) {

    grid-column: 7 / 8;
   
}
.header_container>div:nth-child(4) {

    grid-column: 8 / 9;
    line-height: 300%;
}
.footer_container {

    background-image: url(../img/footer-background-01.jpg) ;
    height: 250px;
}